<template>
	<view class="root">
		<uniPopup id="errorPopup" ref="errorPopup" type="message">
			<uniPopupMessage type="error" :message="errorMessage" :duration="2000"></uniPopupMessage>
		</uniPopup>
		<view class="form-container">
			<scroll-view class="form" :show-scrollbar="true" :scroll-anchoring="true">
				<view class="date">
					<date-input :date="examRecordResult.recordedAt" @open="onDatePickerOpen" @confirm="onDatePickerConfirm" title="检查日期*"></date-input>
				</view>
				<view class="uni-form-item uni-column page" v-for="(page, index) in examRecord.content" :key="index">
					<view class="question" v-for="(question, questionIndex) in page.elements" :key="questionIndex">
						<matrixDynamic :wrapperTitleStyle="wrapperTitleStyle" :wrapperDescriptionStyle="wrapperDescriptionStyle"
							wrapperDescriptionPosition="bottom" :question="question" v-if="question.type === 'matrixdynamic'" 
							:inputStyle="inputStyle" :spliterStyle="spliterStyle" :choiceStyle="choiceStyle"
							@change="onMatrixdynamicChange($event, question)"
							:result="examRecordResult.result[question.name]" />
					</view>
				</view>
				<image-uploader title="上传报告单" titleTips="(供临床医师参考)" @chooseImage="chooseImage" />
			</scroll-view>
		</view>
		<view v-if="showSaveButton" class="bottom">
			<view class="button" @click="finish">保存记录</view>
		</view>
		<uniPopup ref="popup" type="dialog">
		    <uniPopupDialog type="success" title="保存成功" :before-close="true" @close="popupDialogClose" 
				@confirm="popupDialogConfirm" closeText="查看结果" confirmText="继续添加" />
		</uniPopup>
	</view>
</template>

<script>
	import matrixDynamic from '@/packageSubPage/components/questions/matrixdynamic';
	import DateInput from '@/packageSubPage/pages/exam-record/add/date-input';
	import Button from '@/components/button';
	import ImageUploader from '@/packageSubPage/pages/exam-record/add/image-uploader';
	import { EXAM_RECORD_GETTER_GET_ONE, EXAM_RECORD_MUTATION_UPDATE_ONE } from "@/store/examRecordStore";
	import { 
		EXAM_RECORD_RESULT_GETTER_GET_ONE, 
		EXAM_RECORD_RESULT_MUTATION_IN_PROGRESS_UPDATE_ONE,
		EXAM_RECORD_RESULT_GETTER_IN_PROGRESS_GET_ONE,
		EXAM_RECORD_RESULT_MUTATION_ADD_ONE
	} from "@/store/examRecordResultStore";
	import { mapState } from 'vuex';
	import Strings from '@/common/strings';
	import { examRecord } from '@/common/apis/examRecord';
	import { login } from '@/common/helpers/authHelper';
	import { PAGES } from '@/common/constants';
	import { getStaticResource } from "@/common/util";
	import { addExamRecordResult } from "@/common/apis/examRecordResult";
	import { buildInitResult } from "@/packageSubPage/common/helpers/questionHelper";
	import uniPopup from '@/components/uni-popup/uni-popup';
	import uniPopupDialog from '@/components/uni-popup/uni-popup-dialog';
	import uniPopupMessage from '@/components/uni-popup/uni-popup-message';
	import { addVisitSelf } from "@/common/apis/visit";
	import { aliyunPutSignatureUrl } from '@/common/apis/aliyun';
	import { isQuestionPageResultValid } from "@/packageSubPage/common/helpers/questionHelper";

	export default {
		components: { uniPopup, uniPopupDialog, uniPopupMessage, ImageUploader, Button, matrixDynamic, DateInput },
		data() {
			return {
				examRecordId: null,
				examRecordResultId: null,
				currentPage: 0,
				resultPickerIndex: {},
				errorMessage: '',
				showSaveButton: true,
				wrapperTitleStyle: {
					'margin-bottom': '8px',
					'color': '#2E2E2E',
					'font-size': '16px',
					'line-height': '24px',
					'text-overflow': 'ellipsis',
					'display': '-webkit-box',
					'-webkit-line-clamp': '1',
					/* number of lines to show */
					'-webkit-box-orient': 'vertical',
					'overflow': 'hidden',
					'padding': 0,
				},
				wrapperDescriptionStyle: {
					'color': '#979797',
					'font-size': '12px',
					'line-height': '17px',
					'text-overflow': 'ellipsis',
					'display': '-webkit-box',
					'-webkit-line-clamp': '2',
					/* number of lines to show */
					'-webkit-box-orient': 'vertical',
					'overflow': 'hidden',
					'text-align': 'right',
					'margin': '10px 0 0 0',
					'padding': 0,
				},
				inputStyle: {
					'height': '50px',
					'border-radius': '6px',
					'padding': '0 15px',
					'font-size': '16px',
					'background-color': '#F7F8FB',
					'box-sizing': 'border-box',
				},
				spliterStyle: {
					'background-color': '#FFFFFF',
					'height': '10px',
				},
				choiceStyle: {
					'position': 'relative',
					'display': 'flex',
					'flex-direction': 'row',
					'justify-content': 'flex-start',
					'align-items': 'center',
					'height': '50px',
					'border-radius': '6px',
					'padding': '0 14px',
					'background-color': '#F7FAFB',
					'font-size': '16px'
				}
			};
		},
		computed: mapState({
			self: state => state.userStore.self,
			examRecord(state) {
				return this.$store.getters[EXAM_RECORD_GETTER_GET_ONE](this.examRecordId);
			},
			examRecordResult(state) {
				if (!this.examRecordResultId) {
					return this.$store.getters[EXAM_RECORD_RESULT_GETTER_IN_PROGRESS_GET_ONE](this.examRecordId);
				} else {
					let result = this.$store.getters[EXAM_RECORD_RESULT_GETTER_GET_ONE](this.examRecordResultId);
					if (result && result.examRecord) {
						this.$store.commit(EXAM_RECORD_MUTATION_UPDATE_ONE, result.examRecord);
						this.examRecordId = result.examRecord._id;
					}
					return result;
				}
			},
		}),
		async onLoad(option) {
			this.examRecordId = option && option.examRecordId;
			this.examRecordResultId = option && option.examRecordResultId;
			
			if (!this.self._id) await login(this);
			
			// Init
			const result = buildInitResult(this.examRecord);
			const examRecordResult = {
				result, user: this.self._id, examRecord: this.examRecordId
			};			
			this.$store.commit(EXAM_RECORD_RESULT_MUTATION_IN_PROGRESS_UPDATE_ONE, { item: examRecordResult, key: this.examRecordId });
		},
		async onReady() {
			uni.showShareMenu({
				withShareTicket: true,
				menus: ['shareAppMessage', 'shareTimeline'],
				title: PAGES.EXAM_RECORD.title,
				path: PAGES.EXAM_RECORD.path,
				content: PAGES.EXAM_RECORD.description,
			});

			addVisitSelf(this.examRecordResultId || this.examRecordId, PAGES.EXAM_RECORD_ADD.ref);
		},
		onShareAppMessage(res) {
			return {
				title: PAGES.EXAM_RECORD.title,
				path: PAGES.EXAM_RECORD.path,
				content: PAGES.EXAM_RECORD.description,
			}
		},
		methods: {
			async finish() {
				if(!this.validate()) return;
				
				try {
					// TODO: 添加校验
					this.loading = true;
					uni.showLoading({
						title: Strings.GLOBAL.LOADING
					});
					const result = await addExamRecordResult(this.examRecordResult);
					uni.hideLoading();
					this.loading = false;
					if (result.data.addExamRecordResult._id) {
						const examRecordResult = result.data.addExamRecordResult;
						this.$store.commit(EXAM_RECORD_RESULT_MUTATION_IN_PROGRESS_UPDATE_ONE, { item: examRecordResult, key: this.examRecordId });
						this.$store.commit(EXAM_RECORD_RESULT_MUTATION_ADD_ONE, examRecordResult);
						this.$refs.popup.open();
					}
				} catch (e) {
					console.error(e);
				}
			},
			validate() {
				if(!this.examRecordResult.recordedAt) {
					this.errorMessage = `请先输入检查日期。`
					this.$refs.errorPopup.open();
					return false;
				}
				
				let isValid = true;
				const _self = this;
				this.examRecord.content.some(c => {
					const isPageValid = isQuestionPageResultValid(c, _self.examRecordResult.result);
					if(!isPageValid) {
						_self.errorMessage = `请先输入${c.elements[0].title}。`
						_self.$refs.errorPopup.open();
						isValid = false;
					}
					
					return !isPageValid;
				});
				
				return isValid;
			},
			onMatrixdynamicChange(event, question) {
				const { rowIndex, columnIndex, columnName, value } = event;
				const examRecordResult = Object.assign({}, this.examRecordResult);
				examRecordResult.result[question.name][rowIndex][columnName] = value;
				this.$store.commit(EXAM_RECORD_RESULT_MUTATION_IN_PROGRESS_UPDATE_ONE, { item: examRecordResult, key: this.examRecordId });
			},
			onDatePickerConfirm(event) {
				this.showSaveButton = true;
				const fulldate = Date.parse(event.fulldate);
				const examRecordResult = Object.assign({}, this.examRecordResult);
				examRecordResult.recordedAt = fulldate;
				this.$store.commit(EXAM_RECORD_RESULT_MUTATION_IN_PROGRESS_UPDATE_ONE, { item: examRecordResult, key: this.examRecordId });
			},
			onDatePickerOpen(e) {
				this.showSaveButton = false;
			},
			async chooseImage(e) {
				console.log(e);
				await aliyunPutSignatureUrl();
			},
			popupDialogConfirm() {
				this.redirectTo(`${PAGES.EXAM_RECORD_ADD_LIST.path}`);
			},
			popupDialogClose() {
				this.redirectTo(`${PAGES.EXAM_RECORD_RESULT_LIST.path}`);
			},
			redirectTo(url) {
				if (this.navigateFlag) return;
				this.navigateFlag = true;
				uni.redirectTo({ url });
				setTimeout(() => {
					this.navigateFlag = false;
				}, 200);
			}
		} 
	};
</script>

<style scoped>
	
	.root {
		background-color: #f9fafb;
		padding: 12px 10px 12px 10px;
		display: flex;
		flex-direction: column;
		box-sizing: border-box;
		overflow: auto;
		margin-bottom: calc(66px + var(--safe-area-inset-bottom));
		flex: 1;
	}
	
	.form-container {
		display: flex;
		flex: 1;
		flex-direction: column;
		height: 100%;
		width: 100%;
	}
	
	.form {
		display: flex;
		flex: 1;
		flex-direction: column;
		height: 100%;
		width: 100%;
		position: relative;
	}

	.date {
		margin-bottom: 14px;
	}
	
	.page {
		margin-bottom: 14px;
		background-color: #FFFFFF;
		padding: 10px 15px 10px 15px;
		border-radius: 8px;
	}
	
	.question {
		flex-direction: column;
	}

	.bottom {
		position: fixed;
		display: flex;
		background: #fff;
		padding: 10px 18px 10px 18px;
		--safe-area-inset-bottom: env(safe-area-inset-bottom);
		padding-bottom: calc(10px + var(--safe-area-inset-bottom));
		bottom: 0;
		width: 100%;
		z-index: 99999;
		margin: 0 -10px 0 -10px;
		box-sizing: border-box;
		box-shadow: 0 -1px 6px rgba(0, 0, 0, 0.1);
	}

	.button {
		display: flex;
		width: 100%;
		margin: auto;
		height: 46px;
		justify-content: center;
		align-items: center;
		background: #7ED395;
		color: #FFFFFF;
		border-radius: 7px;
		font-size: 16px;
		font-weight: bold;
	}
</style>
